export default{
	data(){
		return {
			modalName: null,
			radio: 'radio1',
			list:[
				{
					name:"修改登录密码",
				},
				{
					name:"通过手机重置登录密码",
				},
				{
					name:"通过邮箱登录密码",
				}
			]
		}
	},
	methods:{
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
			//这个判断是判断用户有没有邮箱绑定
			if(this.useremail){
				this.list = this.list
			}else{
				this.list.pop()
			}
		},
		hideModal(e) {
			this.modalName = null
		},
		RadioChange(e) {
			this.radio = e.detail.value
		},
		//当用户点击了登录
		returnlogin(index,tokey,userid){//index是下标
			uni.navigateTo({
				url:`/components/setcenter/returnloginpasswordall?index=${index}&tokey=${tokey}&id=${userid}`,
				success:()=>{
					this.hideModal()
				}
			})
		}
	},
	created() {
		const _this = this
		
	},
	props:["userphone","useremail","tokey","userid"]
}
